
.wrapper {
  background-color: black;
  min-height: 90vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: white;
}

.menu {
   background-color: darkslateblue;
   height: 300px;
   display: flex;
  width: 300px;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   font-size: 20px;
   color: white;
 }

.display {
  background-color: blueviolet;
  height: 300px;
  display: flex;
  width: 300px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 12pt;
  color: white;
}
.button {
  height: 60px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  font-size: 12pt;
  color: white;
}

.maxError {
    border-color: orangered;
    opacity: 0.5;
}

.startError {
    border-color: orangered;
    opacity: 0.5;
}

.displayError {
    color: orangered;
    opacity: 0.5;
}

